
@import 'style';

.video_wrap{
    background-color: #ccc;
    height:900px;
    margin-bottom: 50px;
    video {
        width: 100%;
    }
}

.main_copy {
    margin-bottom: 150px;

    .m_copy {
        margin-bottom: 20px;
        font-family: 'Roboto', sans-serif;
        font-size: 4.73rem;
        line-height: 1.2;
        font-weight: 700;
        list-style-position: -1px;
    }
    .s_copy {
        font-size: 3rem;
        line-height: 1.25;
    }
}



@media (max-width:480px) {

    .video_wrap{
        height:420px;
        margin-bottom: 50px;
    }
    .main_copy {
        margin-bottom: 70px;
        padding-left: 20px;

        .m_copy{
            font-size: 2.5rem;
        }
        .s_copy{
            font-size: 1.44rem;
        }
    }

    .main .btn_wrap{
        margin: 30px 0 50px;
    }

}