
/****************************/
/* Common Style             */
/****************************/
*{
    box-sizing:border-box;
}
body{
    font-size:1rem;
}
body, input, select, div, textarea, table, button {
    color:$textColor;
}

input[type=text], input[type=password], label, button, select, .select_custom .default {
    color:$formColor;
}

// align
.alC{
    text-align:center !important;
}

// margin
.ml_20{margin-left:20px;}



.hide_sc{
    overflow: hidden;
}


/****************************/
/* heading                  */
/****************************/
h1,h2,h3,h4,h5,h6{
    font-weight: 600;
}

h1{
    // 45px
    font-size:2.8125rem;
}

h2{
    // 36px
    font-size:2.25rem;
}

h3{
    // 26px
    font-size:1.625rem;
}

h4{
    // 20px
    font-size:1.25rem;
}

h5{
    // 18px
    font-size:1.125rem;
}

h6{
    // 16px
    font-size:1rem;
}


.section_copy {
    margin: 150px 0 60px;
    .cate {
        position: relative;
        margin-bottom: 50px;
        padding-left: 90px;
        font-weight: 900;
        font-size: 1.4rem;

        &:before{
            content: '';
            display: inline-block;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            margin: auto;
            background-color: #000;
            width: 70px;
            height: 2px;
        }
    }
    .desc {
        font-size: 3rem;
        line-height: 1.4;
    }
}

.main .section_copy{
    padding-left: 0 !important;
}


@media (max-width:480px) {
    .mhide{
        display:none;
    }
    .mbr{
        display:block;
    }

    .section_copy {
        margin: 50px 0 20px;
        padding-left: 20px;

        .cate {
            margin-bottom: 30px;
            padding-left: 35px;
            font-size: .85rem;
            &:before{
                width: 25px;
            }
        }
        .desc {
            font-size: 1.1rem;
        }
    }

    
}