
@import 'style';

.banner {
    position: relative;
    display: block;
    background-color: #7c7c7c;
    overflow: hidden;

    * {
        color: #fff;
    }

    .inner{
        padding: 130px 90px;
        position: relative;
    }

    .cate {
        font-size: 1.25rem;
        margin-bottom: 27px;
    }
    .tit {
        font-size: 3.438rem;     
        line-height: 1.36;   
        margin-bottom: 20px;
    }

    .cont p {
        font-size: 1.125rem;
        line-height: 1.54;
        font-weight: 300;
    }

    .arrow {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        margin: auto;
        width: 213px;
        height: 213px;
        span {
            position: absolute;
            width: 100%;
            height: 100%;
            background: url('../images/common/footer-arrow-icon.png') 0 0 no-repeat;
            background-size: 100%;
            right: 150px;
            animation: arrowMotion 2s infinite ease-in-out;

            &:nth-child(2){
                top: 0;
                bottom: 0;
                right: 0;
                margin: auto;
            }
            &:nth-child(1){
                top: -150px;
            }
            &:nth-child(3){
                bottom: -150px;
            }
        }

    }
}

@keyframes arrowMotion {
    0%{
        transform:translateX(-100px);
        opacity: 0;
    }
    40%{
        opacity: 1;
    }
    70%{
        opacity: 1;
    }
    100%{
        transform:translateX(0);
        opacity: 0;
    }
}


@media (max-width:480px) {
    .banner{
        
        .inner {
            padding: 50px 70px 100px 20px;
        }
        
        .cate {
            display: none;
        }
        .tit {
            font-size: 1.44rem;     
        }

        .cont p {
            font-size: 0.85rem;
        }

        .arrow{
            transform:scale(.4) translate3D(250px, 100px, 0);
        }
    }
}