
@import 'style';

.item_wrap{
    @include clear; 
    margin: 0 auto;
}

.item_card {
    position: relative;
    width: 770px;
    margin-bottom: 100px;
    // float:left;
}

// reponsive
.item_card,
.grid-sizer { width: 48%; }
.grid-gutter {width: 4%}


.card{
    position: relative;
    display: block;
    color: #171a1c;

    .type{
        margin-bottom: 10px;
        font-family: 'Roboto', sans-serif;
        font-weight: 900;
        font-size: 1rem;
    }
    .title{
        font-size: 1.5rem;
        font-weight: 300;
        line-height: 1.3;
    }
    .img{
        width:100%;
        height: 57%;
        margin-bottom: 40px;
        overflow: hidden;
        background-color: #eee;
        img{
            width:100%;
            transform:scale(1.1);
            transition: $speed;
        }
    }
    .cont{
        margin-top: 20px;
    }

    &:hover{
        img{
            transform:scale(1);
        }
    }
}

.item_view{
    position: relative;
    margin-top: 50px;

    // position: fixed;
    // width: 100% !important;
    // height: 100%;
    // background: #fff;
    // z-index: 999;
    // top:0;
    // left:0;
    // overflow-y: auto;

    .inner{
        max-width: 1920px;
        margin: 0 auto;
        background-color: #fff;
    }

    .visual{
        margin-bottom: 20px;
        img{
            width:100%;
        }
    }
    .desc{
        margin-bottom: 20px;
    }
    .close{
        display: inline-block;
        position: absolute;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        top: 20px;
        right: 20px;
        background-color: rgba(0,0,0,.4);
        &:before,
        &:after{
            content: '';
            display: inline-block;
            position: absolute;
            width: 50%;
            height: 2px;
            top: 50%;
            left: 0;
            right: 0;
            margin: auto;
            border-radius: 5px;
            background-color: #fff;
        }
        &:before{
            transform: rotate(45deg);
        }
        &:after{
            transform: rotate(-45deg);
        }
    }

}


.prinfo{
    position: relative;
    margin: 50px 0;
    @include clear;
    li{
        width: 25%;
        float: left;
        font-size: 1.17rem;
        
        .cate{
            margin-bottom: 10px;
            color: #8e8e8e;
        }
    }
}


.filter{
    margin: 130px 0 60px;

    .filter_tg{
        display: none;
    }
    
    .filters-button-group{
        button {
            min-height: inherit;
            padding: 0;
            margin-right: 50px;
            font-size: 1.25rem;
            font-family: 'Roboto', sans-serif;
            font-weight: 900;
            background: none;
            color: #000;
    
            &.on{
                text-decoration: underline;
            }
            &:last-child{
                margin: 0;
            }
        }
    }
}

.grid-item{
    display: inline-block;
    width: 50px;
    height: 50px;
    border: 1px solid #ccc;
}


.loading_box{
    background-color: rgba(255,255,255,.9);
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
    z-index: 9;
    width: 100%;
    height: 100%;
    font-size: 20px;
    font-weight: bold;
    text-transform: uppercase;

    span{
        display: inline-block;
        animation: loadMotion 1s ease-out infinite alternate;
    }
}

@keyframes loadMotion{
    0%{
        opacity: 0.7;
        transform: scale(1);
    }
    100%{
        opacity: 1;
        transform: scale(1.1);
    }
}


@media (max-width: 1024px) {
    /* 5 columns for larger screens */
    .item_card,
    .grid-sizer { width: 100%; }
    .grid-gutter {width: 0;}

    .item_card {
        margin-bottom: 50px;
    }

    .prinfo{
        li{
            width: 50%;
            margin-bottom: 20px;
        }
    }
}


@media (max-width:480px) {

    .filter{
        margin: 50px 0 40px;

        .filter_tg{
            display: block;
            position: relative;
            padding: 0;
            margin: 0 0 30px 0;
            min-height: inherit;
            font-size: 0.91rem;
            font-weight: 700;
            color: #000;

            &:after{
                content: '';
                display: block;
                width: 100%;
                padding-right: 15px;
                height: 2px;
                background-color: #000;
                position: absolute;
                bottom: -5px;
                left: 0;
            }

            &:hover{
                background: none;
            }
        }
        .filters-button-group{

            button {
                display: block;
                margin: 0 0 20px 0;
                font-size: 0.91rem;
            }
            &.fold{
                display: none;
            }
        }
    }

    .card{
        .img{
            margin-bottom: 20px;
        }
        .type{
            font-size: 0.7rem;
        }
        .title{
            font-size: 1.12rem;
        }
    }

    .item_card {
        margin-bottom: 30px;
    }

        
    .prinfo{
        li{
            width: 100%;
            float: inherit;
            font-size: .78rem;
        }
    }

}