
@import 'style';

header {
   position: relative;
   height: 140px;
   margin-bottom:100px;

   .hd_inner{
        position: relative;
        max-width:1920px;
        padding: 0 0 100px 230px;
        margin: 0 auto;   
   }

   .logo {
        position: absolute;
        left: 230px;
        top: 70px;
        font-size: 1.5rem;
        font-weight: 700;
   }

   .menu {
        position: fixed;
        top: 60px;
        left: 60px;
        display: inline-block;
        width: 60px;
        height: 60px;
        padding: 0;
        z-index: 10;
        background-color: #fff;

        .line {
            @include posCenter;
            display: inline-block;
            position: absolute;
            width: 40%;
            height: 3px;
            background-color: #000;
            transition: $speed;

            &:before,
            &:after{
                content:'';
                display: block;
                position: absolute;
                width: 100%;
                height: 3px;
                background-color: #000;
            }

            &:before{
                top:-9px;
            }

            &:after{
                top:9px;
            }
        }
        &.active .line{
            transform: rotate(90deg);
        }

   }
}

nav {
    position: fixed;
    top: 150px;
    left: 75px;
    visibility: hidden;
    z-index: 10;
    ul {
        transform-origin: 0 0;
        transform: rotate(-90deg) translateX(-100%);
        @include clear;
        li {
            float:left;
            margin-right:40px;
            
            a {
                font-family: 'Roboto', sans-serif;
                font-weight: 700;
                font-size: 1.25rem;
                &.active {
                    text-decoration: underline;
                }
            }
            &:last-child{
                margin-right: 0;
            }
        }
    }
    .nav_close{
        display: none;
    }
}

@media (max-width:1024px) {

    header{
        height: 52px;
        margin-bottom: 70px;
        .menu {
            top: 15px;
            left: 15px;
            width: 40px;
            height: 40px;
            min-height: 40px;
            z-index: 10;
            background-color: #fff;

            .line {
                height: 2px;

                &:before,
                &:after{
                    height: 2px;
                }
                &:before{
                    top:-7px;
                }
    
                &:after{
                    top:7px;
                }
            }
        }

        .logo{
            top: 20px;
            left: 70px;
        }
    }

    nav{
        top: 80px;
        left: 25px;
    }

}

@media (max-width:480px) {

    nav {
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: #000;
        
        transition: .2s ease-out; //draggable move

        .nav_close{
            position: absolute;
            top: 10px;
            left: 10px;
            display: inline-block;
            width: 50px;
            height: 50px;
            min-height: 50px;
            padding: 0;
            background-color: rgba(0,0,0,.4);
            &:before,
            &:after{
                content: '';
                display: inline-block;
                position: absolute;
                width: 50%;
                height: 2px;
                top: 50%;
                left: 0;
                right: 0;
                margin: auto;
                border-radius: 5px;
                background-color: #fff;
            }
            &:before{
                transform: rotate(45deg);
            }
            &:after{
                transform: rotate(-45deg);
            }
        }

        ul {
            padding: 144px 40px;
            transform: rotate(0deg) translateX(0px);
            li {
                display: block;
                float: inherit;
                margin:0 0 50px 0;
                a{
                    font-size: 1.8rem;
                    color: #fff;
                    font-weight: 400;
                }
            }
        }
    }

}