/****************************/
/* button                   */
/****************************/
button{
    position: relative;
    display:inline-block;
    padding:$formHeight/3 $formHeight/2;
    min-height:$formHeight;
    border-radius:$radius;
    background: $gray_f;
    text-align: center;
    font-size: 1.125rem;
    outline: none;
    cursor: pointer;

    &:hover{
        background-color: $gray_d;
        transition:$speed;
    }
    &.color{
        background-color: $mainColor;
        color:#fff;
        &:hover{
            background-color: $darkColor;
        }
    }
    &.border{
        border:1px solid $gray_c;
        background-color: transparent;
        &.color{
            color:$mainColor;
            background-color: transparent;
            border-color:$mainColor; 
        }
        &:hover{
            background-color: $lightColor;
            border-color:$darkColor; 
            color:$darkColor;
        }
    }
    &.round{
        border-radius:30px;
    }

    &.action{
        background-color: $mainColor;
        min-width: 286px;
        text-align: left;
        color:#fff;

        &:after{
            content:'';
            display: inline-block;
            position: absolute;
            width: 34px;
            height: 10px;
            top: 0;
            bottom: 0;
            right: 30px;
            margin: auto;
            background: url('../images/common/common-btn-arrow.png') 0 0 no-repeat;
            background-size: 100%;
        }
        
        &:hover{
            background-color: $darkColor;
        }  
    }

    &.size_s{
        min-height: $formHeight - 15;
        padding:$formHeight/10 $formHeight/3;
        font-size:0.813rem;
    }
    &.size_m{
        padding:$formHeight/10 $formHeight/2;
    }
    &.size_l{
        min-height: $formHeight + 15;
        padding:$formHeight/5 $formHeight;
        font-size:1.25rem;
    }
}

.btn{
    @extend button;
    padding-top:0 !important;
    padding-bottom:0 !important;
    line-height:$formHeight - 2;
    &.size_s{
        line-height: $formHeight - 15 - 2;
    }
    &.size_l{
        line-height: $formHeight + 15 - 2;
    }
}

.btn_wrap{
    margin: 60px 0 100px;
}


@media (max-width:480px) {
    .btn_wrap{
        margin: 30px 0 0;
    }

    .btn{
        &.action{
            background-position: right 16px center;
            padding: 10px 127px 10px 16px;
            min-width: 152px;
            min-height: 38px;
            line-height: 38px;
            font-size: 0.8rem;

            &:after{
                width: 19px;
                height: 8px;
                right: 16px;
            }
        }
        &.wide{
            width: 100%;
        }
    }
    
}