
@import 'style';

/****************************/
/* Layout Style             */
/****************************/
#container{
    position: relative;
    overflow: hidden;

    >.inner{
        position: relative;
        max-width: 1920px;
        padding: 0 0 0 230px;
        margin: 0 auto;   
    }

    &:before{
        content:'';
        position: absolute;
        top:0;
        left:0;
        z-index: -1;
        width:100%;
        height:870px;
        background-color: #f5f5f5;
    }
    &.type_white:before{
        top: 850px;
        height:1358px;
    }
    &.type_gray:before{
        top:0;
    }
}

section{
    padding-top:40px;
    margin-bottom:60px;
    border-top:1px solid $gray_c;
}


@media (max-width:1024px) {
    #container{
        >.inner{
            padding-left:68px;
        }
    }
}

@media (max-width:480px) {
    #container{
        >.inner{
            padding-left:0;
        }

        &:before{
            height:420px;
        }
        &.type_white:before{
            top: 20%;
            height:10%;
        }

        .inner_left{
            padding-left: 68px;
        }
    }
}